@import "normalize.css";

//todo import Fonts
@import url("https://fonts.cdnfonts.com/css/proxima-nova-2");
@import url("https://fonts.googleapis.com/css2?family=IBM+Plex+Sans:ital,wght@0,100;0,300;0,400;0,500;1,400;1,600;1,700&display=swap");

@font-face {
  font-family: "SofiaPro";
  src: url("/assets/fonts/sofiapro-bold.woff2");
  font-weight: 600;
}

@font-face {
  font-family: "SofiaPro";
  src: url("/assets/fonts/sofiapro-semibold.otf");
  font-weight: 600;
}

:root {
  --primary: #fe2c55;
  --black: #000;
  --white: #fff;
  --text-color: #161823;

  --font-family: "Proxima Nova", sans-serif;

  --default-layout-header-height: 60px;
  --default-layout-width: 1150px;
  --default-layout-horizonetal-spacer: 24px;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

html {
  font-size: 62.5%;
}

body {
  font-family: var(--font-family);
  font-size: 1.6rem;
  line-height: 1.5;
  text-rendering: optimizeSpeed;
  color: var(--text-color);
  overflow-y: overlay;
}

//todo scrollbar

html *::-webkit-scrollbar {
  border-radius: 0;
  width: 8px;
}

html *::-webkit-scrollbar-thumb {
  border-radius: 4px;
  background-color: rgba(22, 24, 35, 0.06);
}

html *::-webkit-scrollbar-track {
  border-radius: 0;
  background-color: rgba(0, 0, 0, 0);
}

button,
input,
[tabindex] {
  outline: none;
  border: none;
}

a {
  color: var(--text-color);
  text-decoration: none;
}

body {
  .tippy-box {
    background-color: rgba(84, 84, 84, 0.92);
    // font-weight: 600;
    // font-size: 1em;
    // line-height: 2.2rem;
    border-radius: 8px;
  }

  .tippy-arrow {
    color: rgba(84, 84, 84, 0.92);
  }

  .tippy-box[data-placement^="bottom"] > .tippy-arrow:before {
    top: -8px;
  }

  .tippy-content {
    padding: 8px 9px;
  }
}
